import React, { FormEvent, ChangeEvent } from 'react';
import searchIcon from '../../assets/icons/search_icon.png';
import StorageWorker from '../../util/localStorage';
import { SearchParams, defParams } from '../../data/api-data';

import './searchBar.scss';

type SearchProps = {
  handler: (params: SearchParams) => void;
  searchParams: SearchParams;
};

type SearchState = {
  params: SearchParams;
};

class SearchBar extends React.Component<SearchProps, SearchState> {
  constructor(props: SearchProps) {
    super(props);
    this.state = {
      params: this.props.searchParams,
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event: ChangeEvent) {
    if (event.target instanceof HTMLInputElement) {
      const target = event.target as HTMLInputElement;
      const updatedParams = { ...this.state.params, [target.id]: target.value };
      this.setState({ params: updatedParams });
    }
  }

  handleSubmit(event: FormEvent) {
    event.preventDefault();
    StorageWorker.searchPrompt = this.state.params;
    this.props.handler(this.state.params);
  }

  render() {
    return (
      <form className="search-form" onSubmit={this.handleSubmit}>
        <div className="input-item">
          <label>Beer name:</label>
          <input
            className="search-box"
            type="search"
            autoComplete="hidden"
            placeholder="Search..."
            size={20}
            id="beer_name"
            value={this.state.params.beer_name}
            onChange={this.handleChange}
          />
        </div>

        <div className="input-item">
          <label>Food pairing:</label>
          <input
            className="search-box"
            type="search"
            autoComplete="hidden"
            placeholder="Search..."
            size={20}
            id="food"
            value={this.state.params.food}
            onChange={this.handleChange}
          />
        </div>

        <div className="input-item">
          <label>ABV, greater than:</label>
          <input
            className="num-box"
            type="number"
            id="abv_gt"
            value={this.state.params.abv_gt}
            onChange={this.handleChange}
          />
        </div>
        {/* todo make sliders for ABV/ IBU when libs be allowed */}

        <input className="search-icon" type="image" src={searchIcon} />
      </form>
    );
  }
}

export default SearchBar;
