import React from 'react';
import * as dTypes from '../../data/data';
import BASENAME from '../../basename';

import cartIcon from '../../assets/icons/shop_cart.png';
import inFavIcon from '../../assets/icons/heart_red.png';
import outFavIcon from '../../assets/icons/heart.png';

import './card.scss';

const MAX_DESCRIPTION_LENGTH = 75;
const CURRENCY = 'USD';

function getPriceFormatted(cents: number, currency: string): string {
  const strNum = cents.toString(10);

  return `${strNum.slice(0, -2)}.${strNum.slice(-2)} ${currency.toUpperCase()}`;
}

function getShortItemDescription(description: string): string {
  const shortDescription =
    description.length < MAX_DESCRIPTION_LENGTH
      ? description
      : `${description.slice(0, MAX_DESCRIPTION_LENGTH)} ...`;
  return shortDescription;
}

function getPreviewImgPath(product: dTypes.Product): string {
  let outString = `${BASENAME}/images/placeholder.jpg`;
  if (product.pictures[0]) {
    if (product.pictures[0] === 'blob') {
      outString = product.features.pictureBlob || `${BASENAME}/images/placeholder.jpg`;
    } else {
      outString = `${BASENAME}/images/${product.pictures[0]}`;
    }
  }
  return outString;
}

type cardProps = {
  product: dTypes.Product;
};

function Card(props: cardProps) {
  const { product } = props;
  const previewImgPath = getPreviewImgPath(product);
  const isInFavourites = Math.random() > 0.49;

  return (
    <div className="card-container">
      <div className="image-container">
        <img className="preview-img" src={previewImgPath} alt="product preview" />
      </div>

      <div className="product-info">
        <div className="product-brand">{product.brand}</div>

        <div className="product-name">{product.name}</div>

        <div className="product-description">{getShortItemDescription(product.description)}</div>

        <div className="product-features">
          {product.features.bladeSteel ? (
            <div className="feat">
              <span className="feat-title">Blade Steel:</span>
              {product.features.bladeSteel}
            </div>
          ) : (
            ''
          )}
          {product.features.bladeLengthMm ? (
            <div className="feat">
              <span className="feat-title">Blade Length:</span>
              {`${product.features.bladeLengthMm}mm`}
            </div>
          ) : (
            ''
          )}
          {product.features.weightGramms ? (
            <div className="feat">
              <span className="feat-title">Weight:</span>
              {`${product.features.weightGramms}g`}
            </div>
          ) : (
            ''
          )}
        </div>

        <div className="product-price">{getPriceFormatted(product.priceValue, CURRENCY)}</div>
      </div>

      <div className="product-actions">
        <button className="btn-fav">
          <img
            src={isInFavourites ? inFavIcon : outFavIcon}
            alt="add to cart"
            className="btn-icon"
          />
        </button>

        <button className="btn-addToCart">
          <img src={cartIcon} alt="add to cart" className="btn-icon" />
        </button>
      </div>
    </div>
  );
}

export default Card;
