import React from 'react';

import BeerCard from '../BeerCard/beerCard';
import BeerCardDetailsModal from '../BeerCardDetailsModal/beerCardDetailsModal';
import SearchBar from '../SearchBar/searchBar';
import LoadIndicator from '../LoadIndicator/loadIndicator';

import { MOCK_REQUEST, APICard } from '../../data/api-data';
import { SearchParams } from '../../data/api-data';
import { toggleBodyScroll } from 'util/util';

import './main.scss';

type MainProps = {
  cardList: APICard[];
  handler: (params: SearchParams) => void;
  isLoaded: boolean;
  error: boolean;
  errorMsg: string;
  searchParams: SearchParams;
};

type MainState = {
  modalCardDetailsShow: boolean;
  cardForModal: APICard;
};

class Main extends React.Component<MainProps, MainState> {
  constructor(props: MainProps) {
    super(props);
    this.state = {
      modalCardDetailsShow: false,
      cardForModal: MOCK_REQUEST,
    };

    this.toggleModal = this.toggleModal.bind(this);
  }

  toggleModal = () => {
    this.setState({ modalCardDetailsShow: !this.state.modalCardDetailsShow });
    toggleBodyScroll(!this.state.modalCardDetailsShow);
  };

  render = () => {
    return (
      <div>
        <div className="search-bar">
          <SearchBar handler={this.props.handler} searchParams={this.props.searchParams} />
        </div>

        {!this.props.isLoaded ? (
          <LoadIndicator />
        ) : this.props.error ? (
          <div className="error-box">oops.. {this.props.errorMsg}</div>
        ) : (
          <div className="card-list-container">
            {this.props.cardList.length > 0 ? (
              this.props.cardList.map((item, index) => (
                <BeerCard
                  beer={item}
                  key={index}
                  handleClick={(item: APICard) => {
                    this.setState({ cardForModal: item });
                    this.toggleModal();
                  }}
                />
              ))
            ) : (
              <div>Sorry, no products match your search..</div>
            )}
          </div>
        )}

        <BeerCardDetailsModal
          show={this.state.modalCardDetailsShow}
          beer={this.state.cardForModal}
          onClose={this.toggleModal}
        />
      </div>
    );
  };
}

export default Main;
