import { PUNK_API_ENDPOINT, SearchParams } from '../data/api-data';

export class Api {
  static getFetchOptions(params: SearchParams): string {
    let options = '';
    if (params.beer_name) {
      const names = params.beer_name.toString().replaceAll(' ', '_').toString();
      options += `?beer_name=${names}&`;
    }
    if (params.yeast) {
      const yeast = params.yeast.toString().replaceAll(' ', '_').toString();
      options += `?yeast=${yeast}&`;
    }
    if (params.abv_gt) {
      options += `?abv_gt=${params.abv_gt}&`;
    }
    if (params.abv_lt) {
      options += `?abv_lt=${params.abv_lt}&`;
    }
    if (params.ibu_gt) {
      options += `?ibu_gt=${params.ibu_gt}&`;
    }
    if (params.ibu_lt) {
      options += `?ibu_lt=${params.ibu_lt}&`;
    }
    if (params.page) {
      options += `?page=${params.page}&`;
    }
    if (params.per_page) {
      options += `?per_page=${params.per_page}&`;
    }
    if (params.food) {
      options += `?food=${params.food}&`;
    }
    if (params.brewed_before) {
      options += `?brewed_before=${params.brewed_before}&`;
    }
    if (params.brewed_after) {
      options += `?brewed_after=${params.brewed_after}&`;
    }
    return options;
  }

  static async fetchBeers(params: SearchParams) {
    const request = `${PUNK_API_ENDPOINT}/${this.getFetchOptions(params)}`;
    const response = await fetch(request);
    if (response.ok) {
      const data = await response.json();
      return {
        status: 200,
        data: data,
      };
    }
    return {
      status: response.status,
      data: null,
    };
  }
}

export default Api;
