import React, { ChangeEvent } from 'react';

import './customInput.scss';

type InputProps = {
  rof: React.RefObject<HTMLInputElement>;
  type: string;
  defaultValue: string;
  placeholder: string;
  label: string;
  name: string;
  class?: string;
  onChange: (e: ChangeEvent) => void;
  errorMessage: string;
  hasError: boolean;
};

type InputState = {
  message: string;
};

class CustomInput extends React.Component<InputProps, InputState> {
  constructor(props: InputProps) {
    super(props);
  }

  render() {
    return (
      <div className="form-item">
        <label htmlFor={this.props.name}>{this.props.label}</label>
        <input
          type={this.props.type}
          min="0"
          step="0.1"
          className={this.props.class ? this.props.class : ''}
          placeholder={this.props.placeholder}
          name={this.props.name}
          defaultValue={this.props.defaultValue}
          ref={this.props.rof}
          onChange={this.props.onChange}
        />
        <div className={this.props.hasError ? 'input-err' : 'hidden'} role="errormsg">
          {this.props.errorMessage}
        </div>
      </div>
    );
  }
}

export default CustomInput;
