import React from 'react';

import { APICard } from '../../data/api-data';
import { getEBCColor } from '../../util/util';
import placeholder from '../../assets/icons/placeholder.jpg';

import './beerCradDetailsModal.scss';

type CardProps = {
  beer: APICard;
  onClose: (e: React.MouseEvent) => void;
  show: boolean;
};

class BeerCardDetailsModal extends React.Component<CardProps> {
  constructor(props: CardProps) {
    super(props);
    this.onClose = this.onClose.bind(this);
  }

  onClose = (e: React.MouseEvent) => {
    this.props.onClose && this.props.onClose(e);
  };

  render() {
    if (!this.props.show) {
      return null;
    }
    return (
      <div className="modal">
        <div className="modal-back" onClick={this.onClose}></div>

        <div className="modal-popup">
          <div className="actions">
            <button className="toggle-button" onClick={this.onClose}>
              x
            </button>
          </div>

          <div className="card-details">
            <div className="img-container">
              <img src={this.props.beer.image_url || placeholder} alt="bottle image" />
            </div>

            <div className="info-container">
              <div className="item-name">{this.props.beer.name}</div>

              <div className="item-tagline">{this.props.beer.tagline}</div>

              <div className="item-brewed">
                <span className="atr-label">First brewed:</span>
                {this.props.beer.first_brewed}
              </div>

              <div className="item-abv">
                <span className="atr-label">ABV:</span>
                {this.props.beer.abv}%
              </div>

              <div className="item-ibu">
                <span className="atr-label">IBU:</span>
                {this.props.beer.ibu}
              </div>

              <div className="item-ibu">
                <span className="atr-label">Original Gravity:</span>
                {this.props.beer.ibu}
              </div>

              <div className="item-ibu">
                <span className="atr-label">Final Gravity:</span>
                {this.props.beer.ibu}
              </div>

              <div className="item-ebc">
                <span className="atr-label">EBC Color:</span>
                {this.props.beer.ebc}
                <div
                  className="ebc-box"
                  style={{ backgroundColor: getEBCColor(this.props.beer.ebc) }}
                ></div>
              </div>

              <div className="item-description">{this.props.beer.description}</div>

              <div className="item-food">
                <span className="atr-label">Food Pairing:</span>
                <ul className="bullet-list">
                  {this.props.beer.food_pairing.map((bulletItem, indx) => (
                    <li className="bullet-item" key={'bullet' + indx}>
                      {bulletItem}
                    </li>
                  ))}
                </ul>
              </div>

              <div className="item-tips">{this.props.beer.brewers_tips}</div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default BeerCardDetailsModal;
