import React, { FormEvent, ChangeEvent } from 'react';

import CustomInput from './CustomInput/customInput';
import Card from '../Card/card';

import * as dTypes from '../../data/data';
import switchON from '../../assets/icons/switch-on.png';
import switchOFF from '../../assets/icons/switch-off.png';

import './addCardForm.scss';
import './CustomInput/customInput.scss';

const errorMsgs = {
  pName: 'please enter name',
  pPrice: 'please enter valid price value',
  pSelect: 'please select value from list',
  pYear: 'please specify date',
};

const dummyItem = {
  ID: 'n000',
  name: '',
  modelID: '',
  description: '',
  priceValue: 0,
  category: {
    level0: {
      id: 'a001',
      name: 'Knives and Axes',
    },
    level1: {
      id: 'b001',
      name: 'Knives',
    },
    level2: {
      id: 't001',
      name: 'fixed',
    },
    id: 'a001b001t001',
  },
  brand: dTypes.Brand.Benchmade,
  year: 2022,
  color: dTypes.Color.green,
  countryOfOrigin: dTypes.CounrtyOfOrigin.China,
  pictures: [],
  features: {},
  useCases: [],
};

type FormProps = {
  onSubmit: (item: dTypes.Product) => void;
};

type FormState = {
  item: dTypes.Product;
  canSubmit: boolean;
  gotBlob: boolean;
  switchState: string;
  errName: boolean;
  errPrice: boolean;
  errBrand: boolean;
  errColor: boolean;
  errSteel: boolean;
  errYear: boolean;
  success: boolean;
};

class AddCardForm extends React.Component<FormProps, FormState> {
  productName: React.RefObject<HTMLInputElement>;
  productDescription: React.RefObject<HTMLInputElement>;
  productPrice: React.RefObject<HTMLInputElement>;
  productBrand: React.RefObject<HTMLSelectElement>;
  productBladeSteel: React.RefObject<HTMLSelectElement>;
  productColor: React.RefObject<HTMLSelectElement>;
  productUseCase1: React.RefObject<HTMLInputElement>;
  productUseCase2: React.RefObject<HTMLInputElement>;
  productUseCase3: React.RefObject<HTMLInputElement>;
  productIsActive: React.RefObject<HTMLInputElement>;
  productAvailableFrom: React.RefObject<HTMLInputElement>;
  productImage: React.RefObject<HTMLInputElement>;

  constructor(props: FormProps) {
    super(props);
    this.state = {
      item: dummyItem,
      canSubmit: false,
      gotBlob: false,
      switchState: switchOFF,
      errName: false,
      errPrice: false,
      errBrand: false,
      errColor: false,
      errSteel: false,
      errYear: false,
      success: false,
    };

    this.productName = React.createRef();
    this.productDescription = React.createRef();
    this.productPrice = React.createRef();
    this.productBrand = React.createRef();
    this.productBladeSteel = React.createRef();
    this.productColor = React.createRef();
    this.productUseCase1 = React.createRef();
    this.productUseCase2 = React.createRef();
    this.productUseCase3 = React.createRef();
    this.productIsActive = React.createRef();
    this.productAvailableFrom = React.createRef();
    this.productImage = React.createRef();

    this.handleChange = this.handleChange.bind(this);
    this.handleFileChange = this.handleFileChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event: ChangeEvent) {
    event.preventDefault();
    if (event.target instanceof HTMLInputElement || event.target instanceof HTMLSelectElement) {
      switch (event.target.name) {
        case 'pName':
          this.setState({ errName: this.productName.current?.value === '' });
          break;
        case 'pPrice':
          this.setState({
            errPrice: !this.productPrice || Number(this.productPrice.current?.value) <= 0,
          });
          break;
        case 'pYear':
          this.setState({ errYear: !this.productAvailableFrom.current?.value });
          break;
        case 'pBrand':
          this.setState({ errBrand: !this.productBrand.current?.value });
          break;
        case 'pColor':
          this.setState({ errColor: !this.productColor.current?.value });
          break;
        case 'pSteel':
          this.setState({ errSteel: !this.productBladeSteel.current?.value });
          break;
      }
      this.setState({ canSubmit: true });
      this.setState({ success: false });
    }
  }

  handleFileChange(event: ChangeEvent) {
    if (event.target instanceof HTMLInputElement) {
      this.setState({ gotBlob: true });
    }
  }

  handleSubmit(event: FormEvent) {
    event.preventDefault();

    let isValidated = true;

    if (this.productName.current?.value === '') {
      isValidated = false;
      this.setState({ errName: true });
    }

    if (Number(this.productPrice.current?.value) <= 0) {
      isValidated = false;
      this.setState({ errPrice: true });
    }

    if (!this.productBrand.current?.value) {
      isValidated = false;
      this.setState({ errBrand: true });
    }

    if (!this.productColor.current?.value) {
      isValidated = false;
      this.setState({ errColor: true });
    }

    if (!this.productBladeSteel.current?.value) {
      isValidated = false;
      this.setState({ errSteel: true });
    }

    if (isNaN(new Date(this.productAvailableFrom.current?.value || '').getFullYear())) {
      isValidated = false;
      this.setState({ errYear: true });
    }

    this.setState({ canSubmit: isValidated });

    if (isValidated) {
      const newItem: dTypes.Product = {
        ...dummyItem,
        name: this.productName.current?.value || dummyItem.name,
        description: this.productDescription.current?.value || dummyItem.description,
        priceValue: parseFloat(this.productPrice.current?.value || '') * 100,
        year: new Date(this.productAvailableFrom.current?.value || '').getFullYear(),
        brand: this.productBrand.current?.value as dTypes.Brand,
        color: this.productColor.current?.value as dTypes.Color,
        features: {
          bladeSteel: this.productBladeSteel.current?.value as dTypes.Steel,
          pictureBlob: this.state.gotBlob
            ? URL.createObjectURL(this.productImage.current!.files![0])
            : '',
        },
        pictures: [this.state.gotBlob ? 'blob' : 'placeholder.jpg'],
      };

      this.setState({ item: newItem });
      this.setState({ success: true });
      this.props.onSubmit(newItem);
      const tgt = event.target as HTMLFormElement;
      tgt.reset();
    }
  }

  render() {
    return (
      <form className="add-card-form" onSubmit={this.handleSubmit} encType="multipart/form-data">
        <CustomInput
          type="text"
          label="Product Name:*"
          name="pName"
          placeholder="Product Name"
          rof={this.productName}
          defaultValue={dummyItem.name}
          onChange={this.handleChange}
          errorMessage={errorMsgs['pName']}
          hasError={this.state.errName}
        />

        <CustomInput
          type="textarea"
          label="Product Description:"
          name="pDescription"
          placeholder="Product Description.."
          rof={this.productDescription}
          defaultValue={dummyItem.description}
          class="input-description"
          onChange={this.handleChange}
          errorMessage=""
          hasError={false}
        />

        <CustomInput
          type="number"
          label="Price, USD:*"
          name="pPrice"
          placeholder="Product Price.."
          rof={this.productPrice}
          defaultValue="0"
          onChange={this.handleChange}
          errorMessage={errorMsgs['pPrice']}
          hasError={this.state.errPrice}
        />

        <div className="form-item">
          <label htmlFor="pBrand">Product Brand:*</label>
          <select
            name="pBrand"
            id="brand-select"
            ref={this.productBrand}
            onChange={this.handleChange}
          >
            <option value="">---</option>
            {Object.values(dTypes.Brand).map((item) => (
              <option value={item} key={item}>
                {item}
              </option>
            ))}
          </select>
          <div className={this.state.errBrand ? 'input-err' : 'hidden'}>{errorMsgs['pSelect']}</div>
        </div>

        <div className="form-item">
          <label htmlFor="pColor">Color:*</label>
          <select
            name="pColor"
            id="color-select"
            ref={this.productColor}
            onChange={this.handleChange}
          >
            <option value="" key="unset">
              ---
            </option>
            {Object.values(dTypes.Color).map((item) => (
              <option value={item} key={item}>
                {item}
              </option>
            ))}
          </select>
          <div className={this.state.errColor ? 'input-err' : 'hidden'}>{errorMsgs['pSelect']}</div>
        </div>

        <div className="form-item">
          <label htmlFor="pSteel">Blade Steel:*</label>
          <select
            name="pSteel"
            id="steel-select"
            ref={this.productBladeSteel}
            onChange={this.handleChange}
          >
            <option value="" key="unset">
              ---
            </option>
            {Object.values(dTypes.Steel).map((item) => (
              <option value={item} key={item}>
                {item}
              </option>
            ))}
          </select>
          <div className={this.state.errSteel ? 'input-err' : 'hidden'}>{errorMsgs['pSelect']}</div>
        </div>

        <label className="form-item-switch">
          <input
            className="switch"
            type="checkbox"
            ref={this.productIsActive}
            onChange={() => {
              this.setState({
                switchState: this.productIsActive.current?.checked ? switchON : switchOFF,
              });
            }}
          />
          <span>Active product?</span>
          <img className="switch-icon" src={this.state.switchState} alt="switch" />
        </label>

        <div className="form-item-checkbox">
          <div>Use cases:</div>
          <div className="boxes">
            <label>
              <input type="checkbox" ref={this.productUseCase1} />
              outdoor
            </label>
            <label>
              <input type="checkbox" ref={this.productUseCase2} />
              hunting
            </label>
            <label>
              <input type="checkbox" ref={this.productUseCase3} />
              kitchen
            </label>
          </div>
        </div>

        <div className="form-item">
          <label htmlFor="pYear">Available from:*</label>
          <input
            type="date"
            name="pYear"
            ref={this.productAvailableFrom}
            onChange={this.handleChange}
          />
          <div className={this.state.errYear ? 'input-err' : 'hidden'}>{errorMsgs['pYear']}</div>
        </div>

        <div className="form-item">
          <label htmlFor="pImage">Select image:</label>
          <input
            type="file"
            name="pImage"
            accept="image/png, image/jpeg"
            ref={this.productImage}
            onChange={this.handleFileChange}
          />
        </div>

        <input
          className="btn-submit"
          type="submit"
          value="Add new card"
          disabled={!this.state.canSubmit}
        />

        {this.state.success ? <Card product={this.state.item} /> : ''}
        {this.state.success ? 'card successfully added' : 'please fill the form'}
        <p className="warn">!! ability to add cards is temporarly disabled !!</p>
      </form>
    );
  }
}

export default AddCardForm;
