import { EBC_COLORS } from '../data/api-data';

export const toggleBodyScroll = (stopScroll: boolean) => {
  const appBody = document.body;
  if (stopScroll) {
    appBody?.classList.add('stop-scroll');
  } else {
    appBody?.classList.remove('stop-scroll');
  }
};

export const getEBCColor = (colorIndex: number): string => {
  let color = '#000';
  const maxColorIndex = 30;
  if (colorIndex > 0 && colorIndex <= maxColorIndex) {
    color = EBC_COLORS[colorIndex];
  }
  return color;
};
