import React from 'react';
import { Route, Routes, BrowserRouter } from 'react-router-dom';

import TemplatePage from './components/TemplatePage/templatePage';
import Main from './components/Main/main';
import About from './components/About/about';
import PageNotFound from './components/PageNotFound/pageNotFound';
import AddCardForm from './components/AddCardForm/addCardForm';

import BASENAME from './basename';
import * as dTypes from './data/data';
import { APICard, SearchParams, StatusCodes, defParams } from './data/api-data';
import { Api } from './api/api';
import StorageWorker from './util/localStorage';

import './App.css';
import './css/global.scss';

type MyState = {
  productList: APICard[];
  isDataLoaded: boolean;
  searchParams: SearchParams;
  error: boolean;
  errorMsg: string;
};

class App extends React.Component<unknown, MyState> {
  constructor(props: unknown) {
    super(props);

    this.state = {
      productList: [],
      isDataLoaded: false,
      searchParams: { ...defParams, ...StorageWorker.searchPrompt },
      error: false,
      errorMsg: '',
    };

    this.handleSubmit = this.handleSubmit.bind(this);
    this.fetchData = this.fetchData.bind(this);
    this.searchHandler = this.searchHandler.bind(this);
  }

  async fetchData() {
    try {
      await this.setState({ isDataLoaded: false });
      const apiResponse = await Api.fetchBeers(this.state.searchParams);

      if (apiResponse.status === StatusCodes.OK) {
        setTimeout(() => {
          this.setState({
            productList: apiResponse.data,
            isDataLoaded: true,
            error: false,
            errorMsg: '',
          });
        }, 3000);
      } else {
        throw Error('Request failed.. Error ' + apiResponse.status);
      }
    } catch (err) {
      this.setState({
        error: true,
        errorMsg: (err as Error).message,
        isDataLoaded: true,
      });
    }
  }

  async searchHandler(srchParams: SearchParams) {
    await this.setState({ searchParams: srchParams });
    await this.fetchData();
  }
  async componentDidMount() {
    await this.fetchData();
  }

  handleSubmit(item: dTypes.Product) {
    //TODO submit is disabled as Cards changed to API calls.
    //TODO refactor to match new cards
    console.log(item.ID);
  }

  render() {
    return (
      <BrowserRouter basename={BASENAME}>
        <Routes>
          <Route path="/" element={<TemplatePage />}>
            <Route
              path="/"
              element={
                <Main
                  isLoaded={this.state.isDataLoaded}
                  error={this.state.error}
                  errorMsg={this.state.errorMsg}
                  cardList={this.state.productList}
                  searchParams={this.state.searchParams}
                  handler={this.searchHandler}
                />
              }
            />
            <Route
              path="add"
              element={
                <AddCardForm
                  onSubmit={(newItem: dTypes.Product) => {
                    this.handleSubmit(newItem);
                  }}
                />
              }
            />
            <Route path="about" element={<About />} />
            <Route path="*" element={<PageNotFound />} />
          </Route>
        </Routes>
      </BrowserRouter>
    );
  }
}

export default App;
