import React from 'react';
import { APICard } from '../../data/api-data';

import inFavIcon from '../../assets/icons/heart_red.png';
import outFavIcon from '../../assets/icons/heart.png';
import placeholder from '../../assets/icons/placeholder.jpg';

import './beerCard.scss';

type CardProps = {
  beer: APICard;
  handleClick: (item: APICard) => void;
};

const BeerCard = (props: CardProps) => {
  const { beer, handleClick } = props;
  const isInFavourites = Math.random() > 0.49;

  return (
    <div className="beer-card-container" onClick={() => handleClick(beer)}>
      <div className="image-container">
        <img className="preview-img" src={beer.image_url || placeholder} alt="product preview" />
      </div>

      <div className="product-info">
        <div className="product-name">{beer.name}</div>

        <div className="product-tagline">{beer.tagline}</div>

        <div className="product-introduced">First brewed - {beer.first_brewed}</div>
      </div>

      <div className="product-actions">
        <button
          className="btn-fav"
          onClick={(e) => {
            e.stopPropagation();
          }}
        >
          <img
            src={isInFavourites ? inFavIcon : outFavIcon}
            alt="add to cart"
            className="btn-icon"
          />
        </button>
      </div>
    </div>
  );
};

export default BeerCard;
