export const PUNK_API_ENDPOINT = 'https://api.punkapi.com/v2/beers';

export enum Methods {
  POST = 'POST',
  GET = 'GET',
  PUT = 'PUT',
  DELETE = 'DELETE',
}

export enum StatusCodes {
  BadRequest = 400,
  NotFound = 404,
  Unauthorized = 401,
  Forbidden = 403,
  ServerError = 500,
  OK = 200,
}

export type SearchParams = {
  beer_name?: string;
  yeast?: string;
  brewed_before?: string; // date format is MM-YYYY
  brewed_after?: string; // date format is MM-YYYY
  food?: string;
  abv_gt?: number;
  abv_lt?: number;
  ibu_gt?: number;
  ibu_lt?: number;
  page?: number;
  per_page?: number;
};

